@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{
	@include m.tablet {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		min-height:100vh;
	}

	.imagen{
		height: 30rem;
		background-image: url('../img/1.jpg');
		background-size: cover;
		background-position:center center;
		@include m.tablet{ //cuando estamos en pantalla desktop le agregamos el media query
			height: auto !important; //auto para que tome todo el espacio disponible
		}
	}

	.app{
		width: 95%;
		margin: 0 auto !important;
		padding: 3rem 0; //lo mismo que 30px; rem = px * 10
	}
}