@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.check {
  font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor:pointer;
  box-shadow:  9px 9px 18px #262c3e,
    -9px -9px 18px #30384e;
  color: #0ad406;
  margin: 20px;
}
.check:hover{
  background-color: rgba(147, 209, 117, 0.50);
  transition:0.5s;
}

.color {
  animation: color 2s linear infinite;
}


.info {
  font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor:pointer;
  box-shadow:  9px 9px 18px #262c3e,
    -9px -9px 18px #30384e;
  color: rgb(82, 125, 243);
  margin: 20px;
}

.spin {
  animation: spin 2s linear infinite;
}

.info:hover
{
  background-color: rgba(82, 125, 243, 0.50);
  transition:0.5s;
}

.warning {
  font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor:pointer;
  box-shadow:  9px 9px 18px #262c3e,
    -9px -9px 18px #30384e;
  color: rgb(247, 149, 72);
  margin: 20px;
}

.warning:hover{
  background-color: rgba(247, 149, 73, 0.50);
  transition:0.5s;
}


.rotate {
  animation: rotate 2s linear infinite;
}
.danger {
  font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor:pointer;
  box-shadow:  9px 9px 18px #262c3e,
    -9px -9px 18px #30384e;
  color: rgb(255, 10, 1);
  margin: 20px;
}

.danger:hover
{
  background-color: rgba(255, 10, 1, 0.33);
  transition:0.5s;
}


.shine {
  animation: shine 2s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes color {
  50% {
   color: #2B3246;
  }
  100% {
    color: #0ad406;
  }
}

@keyframes rotate {
 0% {
    transform: rotate(0deg);
  }
  20% {
    transform: rotate(90deg);
  }
  40% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(-90deg);
  }
}

@keyframes shine {
  0% {
   color: #2B3246;
  }
  50% {
    text-shadow:
      0 0 5px #fff,
      0 0 10px #fff,
      0 0 40px red,
      0 0 60px red,
      0 0 80px red;
  }
}