/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 30rem;
  background-image: url("../img/1.jpg");
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto !important;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto !important;
  padding: 3rem 0;
}

.seccion {
  padding: 5rem 0;
  display: none;
  /*vamos a ocultar los hijos 2 y 3 de seccion*/
  /*&:nth-child(2),
  &:nth-child(3){
  	display: none; //no se muestran
  }*/
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  border-radius: 1rem;
  padding: 1.25rem;
}
.campo input:disabled {
  background-color: #a0a48b;
  color: #FFFFFF;
  font-weight: 700;
  cursor: not-allowed;
}

.boton {
  background-color: #0da6f3;
  padding: 1rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  color: #FFFFFF;
  text-decoration: none;
  margin-bottom: 2.5rem;
  text-align: center;
}
.acciones a span {
  font-weight: 700;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.check {
  font-family: "Ubuntu", sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor: pointer;
  box-shadow: 9px 9px 18px #262c3e, -9px -9px 18px #30384e;
  color: #0ad406;
  margin: 20px;
}

.check:hover {
  background-color: rgba(147, 209, 117, 0.5);
  transition: 0.5s;
}

.color {
  animation: color 2s linear infinite;
}

.info {
  font-family: "Ubuntu", sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor: pointer;
  box-shadow: 9px 9px 18px #262c3e, -9px -9px 18px #30384e;
  color: rgb(82, 125, 243);
  margin: 20px;
}

.spin {
  animation: spin 2s linear infinite;
}

.info:hover {
  background-color: rgba(82, 125, 243, 0.5);
  transition: 0.5s;
}

.warning {
  font-family: "Ubuntu", sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor: pointer;
  box-shadow: 9px 9px 18px #262c3e, -9px -9px 18px #30384e;
  color: rgb(247, 149, 72);
  margin: 20px;
}

.warning:hover {
  background-color: rgba(247, 149, 73, 0.5);
  transition: 0.5s;
}

.rotate {
  animation: rotate 2s linear infinite;
}

.danger {
  font-family: "Ubuntu", sans-serif;
  font-size: 2rem;
  font-weight: 500;
  border-radius: 5px;
  width: auto !important;
  height: 70px;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #2B3246;
  cursor: pointer;
  box-shadow: 9px 9px 18px #262c3e, -9px -9px 18px #30384e;
  color: rgb(255, 10, 1);
  margin: 20px;
}

.danger:hover {
  background-color: rgba(255, 10, 1, 0.33);
  transition: 0.5s;
}

.shine {
  animation: shine 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes color {
  50% {
    color: #2B3246;
  }
  100% {
    color: #0ad406;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  20% {
    transform: rotate(90deg);
  }
  40% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(-90deg);
  }
}
@keyframes shine {
  0% {
    color: #2B3246;
  }
  50% {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 40px red, 0 0 60px red, 0 0 80px red;
  }
}
/*Modificamos la clase tabs*/
@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 1rem;
  color: #FFFFFF;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #0a85c3;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}